@import url(https://fonts.googleapis.com/css?family=Russo+One);
#root {
  width: 100%;
}

body {
  /*background: lighten($blackStyle, 0%);*/
  background: url(/markdownpreviewer/static/media/back1.0565cce3.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  margin: 10px 0;
}

::-webkit-scrollbar-corner {
  border-radius: 2px;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  border-radius: 20px;
  background: #2c2b2b;
}

::-webkit-scrollbar-thumb {
  background: #990202;
  border-radius: 20px;
  width: 10px;
  cursor: pointer;
}

::-webkit-scrollbar-thumb:hover {
  background: red;
  border-radius: 20px;
  width: 10px;
}

#preview, .title {
  display: inline-block;
}

.colorScheme, .editorWrap textarea, .previewWrap {
  background-color: #1a1a1a;
  border: 1px solid black;
  border-radius: 10px;
  border-top: none;
  box-shadow: 1px 1px 10px 2px black;
  color: rgba(255, 255, 255, 0.877);
}

.toolbar {
  position: relative;
  background: #d20000;
  background: linear-gradient(90deg, #d20000 0%, #d20000 50%, #fcb045 100%);
  padding: 4px 4px 3px 3px;
  border: 1px solid black;
  border-radius: 10px;
  box-shadow: 1px 1px 10px 2px black;
  font-family: Russo One;
  font-size: 15px;
  cursor: pointer;
}

.toolbar i {
  width: 25px;
  color: black;
  margin-left: 5px;
}

.toolbar .fa-expand,
.toolbar .fa-compress {
  position: absolute;
  right: -5px;
  font-size: 22px;
  margin-right: 5px;
}

.fa-retweet:hover,
.fa-compress:hover,
.fa-expand:hover {
  color: #ff7575;
  cursor: pointer;
  font-size: 22px;
  margin-right: 5px;
}

.fa-file-code {
  margin-right: 3px;
  font-size: 22px;
}

#container {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 10px auto;
}

.editorWrap {
  position: fixed;
  width: 400px;
  min-width: 200px;
  margin: 30px;
  z-index: 1;
}

.editorWrap .toolbar {
  width: 99%;
}

.editorWrap textarea {
  width: 99%;
  height: 500px;
  min-height: 200px;
  margin-bottom: -5px;
  padding-left: 5px;
  padding-top: 5px;
  resize: vertical;
  outline: none;
  font-size: 15px;
}

.previewWrap {
  width: 800px;
  min-height: 200px;
  margin-left: 450px;
  margin-top: 30px;
}

.previewWrap .toolbar {
  width: 100%;
  padding-right: 17px;
  border-right: 0px;
  border-left: 0px;
}

.previewWrap #preview {
  margin-left: 5px;
  margin-top: -10px;
  width: 100%;
}

.maximized {
  width: 96%;
  min-height: 100vh;
  margin: 10px auto;
}

.maximized textarea {
  min-height: 90vh;
  resize: none;
}

.maximized1 {
  width: 96%;
  min-height: 100vh;
  margin: 20px auto !important;
  padding-top: 0px;
}

.maximized1 textarea {
  min-height: 95vh;
  resize: none;
}

.maximized1 img {
  width: 350px;
  margin-left: 10px;
}

.hide {
  display: none;
}

#preview blockquote {
  border-left: 3px solid #a3c705;
  color: #fc7a00;
  padding-left: 5px;
  margin-left: 25px;
}

#preview code {
  background: #868383;
  padding: 1px 4px 2px 4px;
  font-size: 15px;
}

#preview pre {
  background: #868383;
  padding: 5px 0 5px 5px;
  margin-right: 10px;
}

#preview h1 {
  border-bottom: 2px solid #db0000;
  width: 99%;
}

#preview h2 {
  border-bottom: 1px solid #db0000;
  width: 99%;
}

#preview table {
  border-collapse: collapse;
}

#preview td, #preview th {
  border: 2px solid #db0000;
  padding-left: 5px;
  padding-right: 5px;
}

@media screen and (max-width: 1300px) {
  #container {
    margin-right: auto;
    margin-left: auto;
  }
  .previewWrap {
    margin-top: 290px;
    margin-right: auto;
    margin-left: 5px;
  }
  .editorWrap {
    margin: 10px auto;
    border-bottom: 2px solid white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .editorWrap textarea {
    height: 200px;
  }
}

@media screen and (max-with: 850px) {
  .previewWrap {
    width: 630px;
    display: block;
  }
  .editorWrap {
    width: 550px;
    display: block;
  }
}

@media screen and (max-width: 500px) {
  .editorWrap {
    width: 95%;
    box-sizing: content-box;
    margin-left: 10px;
    margin-right: 10px;
  }
  .previewWrap {
    width: 90%;
    margin-top: 290px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 2px;
    padding-right: 2px;
    box-sizing: content-box;
    overflow: hidden;
  }
  .converter {
    margin: 0;
    width: 0px;
  }
  img {
    width: 95%;
    margin: auto;
  }
  ::-webkit-scrollbar {
    width: 17px;
  }
  table {
    border-collapse: separate;
  }
  td, th {
    border: 1px solid #db0000 !important;
    padding-left: 3px !important;
    padding-right: 1px !important;
  }
}
